""
import { useEffect, useState } from "react";
import axios from "axios";
import { CheckCircle, Clock, CreditCard } from "lucide-react";
import { toast } from "react-toastify";

export default function OrderTable() {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchOrders = async () => {
      try {
        const res = await axios.get("http://localhost:3001/api/order");

        let data = res.data;
        if (Array.isArray(data)) {
          setOrders(data);
        } else if (Array.isArray(data.orders)) {
          setOrders(data.orders);
        } else {
          setOrders([data]);
        }

        setLoading(false);
      } catch (error) {
        console.error(error);
        toast.error("Failed to fetch orders!");
        setLoading(false);
      }
    };

    fetchOrders();
  }, []);

  const getStatusIcon = (status) => {
    switch (status) {
      case "Pending":
        return <Clock className="text-yellow-500" />;
      case "Completed":
        return <CheckCircle className="text-green-500" />;
      default:
        return <Clock className="text-gray-400" />;
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen text-lg text-gray-600">
        ‚è≥ Loading orders...
      </div>
    );
  }

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <div className="max-w-6xl mx-auto bg-white shadow-lg rounded-2xl p-6">
        <h2 className="text-2xl font-semibold mb-4 text-gray-800">
          üßæ Order Details
        </h2>

        {orders.length === 0 ? (
          <p className="text-gray-500 text-center py-10">No orders found.</p>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full border border-gray-200 rounded-lg overflow-hidden">
              <thead className="bg-gray-100 text-gray-700 text-sm uppercase">
                <tr>
                  <th className="px-6 py-3 text-left">User</th>
                  <th className="px-6 py-3 text-left">Items</th>
                  <th className="px-6 py-3 text-center">Total</th>
                  <th className="px-6 py-3 text-left">Address</th>
                  <th className="px-6 py-3 text-center">Payment</th>
                  <th className="px-6 py-3 text-center">Status</th>
                  <th className="px-6 py-3 text-center">Order ID</th>
                  <th className="px-6 py-3 text-center">Created</th>
                </tr>
              </thead>
              <tbody className="text-sm text-gray-800">
                {orders.map((order, index) => (
                  <tr
                    key={index}
                    className="border-t hover:bg-gray-50 transition-all duration-200"
                  >
                    {/* üßç User Info */}
                    <td className="px-6 py-3">
                      <div className="flex flex-col">
                        <span className="font-semibold">
                          {order.userId?.username || "Unknown User"}
                        </span>
                        <span className="text-gray-500 text-xs">
                          {order.userId?.email || "No email"}
                        </span>
                      </div>
                    </td>

                    {/* üì¶ Items */}
                    <td className="px-6 py-3">
                      {order.items?.map((item, i) => (
                        <div key={i}>
                          {item.name || "Item"} - ‚Çπ{item.price || 0}
                        </div>
                      ))}
                    </td>

                    {/* üí∞ Total */}
                    <td className="px-6 py-3 text-center font-semibold text-blue-600">
                      ‚Çπ{order.totalAmount}
                    </td>

                    {/* üè† Address */}
                    <td className="px-6 py-3">{order.address}</td>

                    {/* üí≥ Payment */}
                    <td className="px-6 py-3 text-center">
                      <div className="flex items-center justify-center gap-2">
                        <CreditCard className="text-gray-500" size={16} />
                        <span>{order.paymentMethod}</span>
                      </div>
                    </td>

                    {/* üìå Status */}
                    <td className="px-6 py-3 text-center">
                      <div className="flex items-center justify-center gap-2">
                        {getStatusIcon(order.status)}
                        <span
                          className={`font-medium ${
                            order.status === "Pending"
                              ? "text-yellow-600"
                              : "text-green-600"
                          }`}
                        >
                          {order.status}
                        </span>
                      </div>
                    </td>

                    {/* üßæ Razorpay Order ID */}
                    <td className="px-6 py-3 text-center font-mono text-gray-500">
                      {order.razorpayOrderId}
                    </td>

                    {/* üìÖ Date */}
                    <td className="px-6 py-3 text-center text-gray-500">
                      {new Date(order.createdAt).toLocaleString()}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}
